___CSS_LOADER_EXPORT___.push([module.id, `/* Menu responsive */

.hamburger {
  width: 100%;
  display: flex;
  font-weight: 600;
  font-size: 20px;
  justify-content: space-between;
  position: fixed;
  z-index: 999;
  background: rgba(216, 232, 229, 0);
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(4.5px);
  -webkit-backdrop-filter: blur(4.5px);
}
.drawer_resp {
  top: 0;
  width: 100%;
  display: flex;
  font-weight: 600;
  font-size: 20px;
  text-transform: uppercase;
  margin-top: 65px;
  background-color: #f5f7f8;
}

.drawer_list {
  width: 100%;
  padding: 0;
  margin: 0;
  list-style: none;
}
.drawer_item {
  line-height: 24px;
  text-transform: uppercase;
  box-sizing: border-box;
  display: inline-block;
  background-color: #ff9a3c;
}
.hamburger button {
  margin-left: 20px;
  font-size: 25px;
  text-decoration: none;
  text-align: center;
  color: #ff6f3c !important;
  background-color: transparent;
  cursor: pointer;
  border: 0;
}
.drawer_resp a {
  display: inline-block;
  padding: 1.3em;
  text-decoration: none;
  color: #252a34;
}

.drawer_resp a:hover {
  color: #ffc93c;
}
