___CSS_LOADER_EXPORT___.push([module.id, `.lists {
  margin: 32px auto;
  text-align: left;
  display: grid;
  grid-row-gap: 20px;
}

.list-item {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  width: 100%;
  border-radius: 5px;
  overflow: hidden;
}

.list-item a {
  text-decoration: none;
  color: black;
  cursor: pointer;
}

.list-item:hover {
  -webkit-transform: translate(0, -10px);
  transform: translate(0, -10px);
  transition: transform 0.5s ease;
}

.list-item__content {
  padding: 16px;
}

.list-item__restaurant {
  font-size: 1em;
  color: #999;
}

.list-item__restaurant-rate {
  font-size: 1em;
  text-decoration: none;
}

.list-item__content h2 {
  font-weight: 500;
  margin-top: 10px;
  font-size: 30px;
  transition: 0.3s opacity;
}

.list-item__title:hover {
  opacity: 0.5;
}

.list-item__title a {
  text-decoration: none;
  color: inherit;
}

.list-item__description {
  margin-top: 10px;
  font-size: 15px;
  line-height: 1.5em;
  display: -webkit-box;
  -webkit-line-clamp: 4; /* number of lines to limit text to */
  -webkit-box-orient: vertical;
  overflow: hidden;
}

.list-item__thumbnail {
  width: 100%;
  height: 300px;
  background-size: cover;
  object-fit: cover;
  object-position: center;
}

.list-thumbnail {
  position: relative;
}

.list-item_rate {
  background-color: #155263;
  top: 20px;
  right: 0;
  border-radius: 5px 0px 0px 5px;
  color: #f4ce14;
  font-weight: 500;
  font-size: 16px;
  padding: 10px;
  position: absolute;
}

.list-city {
  font-size: 16px;
  text-transform: uppercase;
  font-weight: 500;
  color: #ff6f3c;
  text-decoration: none;
}
