___CSS_LOADER_EXPORT___.push([module.id, `.detail {
  margin-top: 32px;
}

.restaurant-picture {
  width: 100%;
  height: 400px;
  background-size: cover;
  object-fit: cover;
  object-position: center;
}

.detailRestaurant {
  display: grid;
  text-align: left;
}

.detailRestaurant h2 {
  font-size: 2em;
}

.detailRestaurant p {
  font-size: 1.5em;
}

.detailRestaurant i {
  color: #ff6f3c;
}

.detailRestaurant-info {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  margin-top: 10px;
}

.detailRestaurant-info p {
  margin: 10px;
}

.detailRestaurant-name {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.detailRestaurant-address {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.detailRestaurant-rate {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.restaurant-description-title {
  text-align: center;
  margin-bottom: 8px;
  background-color: #ffc93c;
}

.restaurant-description {
  text-align: justify;
}

.restaurant-category {
  text-align: left;
}

.restaurant-category-title {
  margin-top: 10px;
  margin-bottom: 10px;
  border-radius: 12px;
  border: 2px solid #ff6f3c;
  color: #ff6f3c;
  display: inline-block;
  padding: 5px 15px;
  font-size: 1em;
}

.detailRestaurant-menu h2 {
  background-color: #ffc93c;
  font-size: 2em;
}

.restaurant-menu h3 {
  text-align: center;
  font-size: 1.5em;
}

.restaurant-menu p {
  font-size: 1.5em;
  margin: 5px;
}

.restaurant-menu ul {
  margin: 0 auto;
}

.restaurant-menu li {
  list-style-type: none;
  text-align: left;
}

.restaurant-menu i {
  color: #ff6f3c;
}

.restaurant-review-title {
  text-align: center;
  font-size: 2em;
  margin-bottom: 10px;
}

.review-title {
  font-size: 1.2em;
}

.restaurant-review {
  margin-bottom: 20px;
  margin-top: 10px;
}

.restaurant-review i {
  color: #ff6f3c;
}

.restaurant-review:hover {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  transition: transform 0.5s ease;
}

.review-header {
  display: flex;
  justify-content: space-between;
  border-bottom: 1px solid black;
  padding-bottom: 10px;
  font-size: 1.2em;
}

.review-name {
  font-weight: bold;
  font-size: 1.2rem;
  text-align: left;
}

.review-date {
  font-size: 1rem;
}

.review-comment {
  text-align: left;
  padding-top: 10px;
  font-size: 1.5em;
}

.form-review {
  padding: 10px 20px;
  border: 1px solid;
  border-radius: 5px;
}

.form-review h2 {
  font-size: 2em;
  padding-bottom: 10px;
}

.form-group {
  flex-direction: row;
  text-align: left;
  margin-top: 10px;
}

.form-group label {
  font-size: 1.8em;
}

.form-control {
  width: 80%;
  padding: 5px;
  margin: 5px;
  font-size: 1rem;
  min-height: 44px;
  border-top: 0;
  border-left: 0;
  border-right: 0;
  border-bottom: 2px solid #252a34;
  background-color: transparent;
  outline: none;
}

.form-control::placeholder {
  color: #ff6f3c;
  opacity: 70%;
}

.submitReview {
  margin-top: 10px;
  padding: 10px;
  border-radius: 40px;
  border: 2px solid #ff6f3c;
  background-color: #ff6f3c;
  color: #eaeaea;
  font-size: 1rem;
  font-family: "Nunito", sans-serif;
  font-weight: bold;
  cursor: pointer;
  margin: auto;
}

.submitReview:hover {
  background-color: #ffc93c;
  color: #ff6f3c;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  transition: transform 0.5s ease;
}

.like {
  font-size: 18px;
  position: fixed;
  bottom: 16px;
  right: 16px;
  background-color: #ff6f3c;
  color: #f5f7f8;
  border: 1px solid #ff6f3c;
  border-radius: 50%;
  width: 55px;
  height: 55px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
}

.like:hover {
  background-color: #ffc93c;
  color: #ff6f3c;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  transition: transform 0.5s ease;
}
