___CSS_LOADER_EXPORT___.push([module.id, `@media screen and (max-width: 549px) {
  .nav {
    display: none;
  }

  .drawer_resp {
    z-index: 10;
    background-color: #fff;
    width: 100%;
    position: fixed;
    -webkit-transform: translate(-100%, 0);
    transform: translate(-100%, 0);
    transition: transform 0.3s ease;
  }

  .open {
    -webkit-transform: translate(0, 0);
    transform: translate(0, 0);
  }

  .drawer_item {
    display: list-item;
    color: black !important;
    width: 100%;
    text-align: left;
  }

  .hamburger a {
    display: inline-block;
    font-size: 30px;
    text-decoration: none;
    color: #ff6f3c !important;
    font-family: "Kaushan Script", cursive;
  }

  .content__heading {
    text-align: center;
    margin-bottom: 20px;
  }

  .hero {
    background: linear-gradient(rgba(0, 0, 5, 0.5), rgba(0, 0, 5, 0.5)),
      url(${___CSS_LOADER_URL_REPLACEMENT_0___}) repeat center;
  }
}

@media screen and (max-width: 649px) {
  .submitReview {
    display: flex;
    justify-content: center;
    align-items: center;
  }
}

@media screen and (min-width: 550px) {
  .hamburger {
    display: none;
  }

  .drawer_resp {
    display: none;
  }
}

@media screen and (min-width: 650px) {
  .list-item__content {
    padding: 16px 32px 32px 32px;
  }
  .list-item__title {
    font-size: 18px;
  }
  .list-item__description {
    font-size: 14px;
  }
  .restaurant-review {
    padding: 10px 20px;
  }
  .form-group {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}

@media screen and (max-width: 700px) {
  .restaurant-menu h3 {
    text-align: left;
    margin: 10px;
  }

  .detailRestaurant-info {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}

@media screen and (min-width: 700px) {
  .lists {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-column-gap: 10px;
    grid-row-gap: 16px;
  }
  .restaurant-menu {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-column-gap: 10px;
    grid-row-gap: 16px;
  }
}

@media screen and (min-width: 800px) {
  .nav_list {
    max-width: 800px;
    margin: 0 auto;
  }
}

@media screen and (min-width: 900px) {
  .lists {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
  }
  .restaurant-menu {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-column-gap: 10px;
    grid-row-gap: 16px;
  }
}

@media screen and (min-width: 1200px) {
  main {
    max-width: 1200px;
  }
}
