  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  font-family: "Nunito", sans-serif;
  font-size: 12px;
  font-weight: 550;
  color: #45474b;
  background-color: #f5f7f8;
}

